// Bad Ass Things
import Alpine from 'alpinejs';

window.Alpine = Alpine;
Alpine.start();

import '../app.js';

// Vendors
import jQuery from 'jquery';
import 'jquery-ui/ui/widgets/datepicker.js';
import 'jquery-ui/ui/widgets/dialog.js';
import 'jquery-ui/ui/widgets/autocomplete.js';

require('chosen-js');
import '../scripts/components-v2.js';
import '../lib/snackbar/ch32.snackbar.js';

// Css Imports
import '../styles/main.css';
import '../lib/snackbar/ch32.snackbar.css';

// make and input field markup with function for change font size



jQuery(document).ready(function () {

    // Snackbar
    jQuery(document).on('click', '.copy-toast', function (e) {
        e.preventDefault();

        let time = 3000;

        // Create Settings for Copy Snackbar
        jQuery.snackbar({
            heading: 'Notification',
            text: 'Unique IBAN copied to clipboard',
            position: 'bottom-center',
            loader: false,
            stack: true,
            allowSnackbarClose: false,
            icon: 'success',
            hideAfter: time,
        });

        // Custom Configurations
        jQuery('.ch32-snackbar-wrap').focus().removeAttr('tabindex').removeAttr('aria-hidden');
        jQuery('.active').removeClass('active');
        jQuery(this).addClass('active');
        jQuery('body').addClass('ch32-snackbar-active');

        setTimeout(function () {
            jQuery('.ch32-snackbar-wrap').attr('tabindex', '-1').attr('aria-hidden', true);
            jQuery('body').removeClass('ch32-snackbar-active');
            jQuery('.active').focus().removeClass('active');
        }, time);
    });

    // Theme Class Element
    const themeEl = 'html';

    // Radio checked by local storage value
    jQuery('input[name="theme"]').each(function () {
        if (localStorage.getItem('theme') === jQuery(this).val()) {
            jQuery(this).prop('checked', true);
        }
    });

    // Dark and Light Theme Switcher
    jQuery('input[name=theme]').change(function () {
        if (jQuery(this).val() === 'dark') {
            jQuery(themeEl).addClass('dark');
            jQuery.snackbar({
                heading: 'Notification',
                text: 'Dark Theme Enabled',
                position: 'top-right',
                loader: false,
                stack: true,
                hideAfter: 5000,
            });
            jQuery(themeEl).removeClass('light');
            localStorage.setItem('theme', 'dark');
        } else {
            jQuery(themeEl).addClass('light');
            jQuery.snackbar({
                heading: 'Notification',
                text: 'Light Theme Enabled',
                position: 'top-right',
                loader: false,
                stack: true,
                hideAfter: 5000,
            });
            jQuery(themeEl).removeClass('dark');
            localStorage.setItem('theme', 'light');
        }
    });

    //  Dark and Light local storage
    if (localStorage.getItem('theme') === 'dark') {
        jQuery(themeEl).addClass('dark');
        jQuery('#darkTheme').prop('checked', true);
    } else {
        jQuery(themeEl).addClass('light');
        jQuery('#lightTheme').prop('checked', true);
    }

    // init chosen select
    jQuery('select')
        .chosen({
            placeholder_text_single: "Select an option",
            allow_single_deselect: true,
        });

    // Initialize the datepicker with open to current date
    jQuery('.datepicker').datepicker({
        showOtherMonths: true,
        selectOtherMonths: true,
        showButtonPanel: true,
        dateFormat: "dd-mm-yy",
        nextText: "",
        prevText: "",
        onUpdateDatepicker: function () {
            jQuery('.ui-datepicker.ui-datepicker-multi').wrapInner('<div class="tailGroup flex"></div>');
            jQuery('.ui-datepicker.ui-datepicker-multi  .ui-datepicker-buttonpane').insertAfter('.ui-datepicker .tailGroup');
            jQuery('.ui-datepicker.ui-datepicker-multi  .ui-datepicker-row-break').insertAfter('.ui-datepicker .tailGroup');
            jQuery('.ui-datepicker-calendar').wrap('<div class="ui-datepicker-calendar-container"></div>');
            jQuery('.ui-datepicker-header > .ui-datepicker-prev').append('<svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-gray-500 hover:text-violet-700 cursor-pointer" fill="none" viewBox="0 0 24 24" stroke="currentColor">\n' +
                '  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />\n' +
                '</svg>' +
                '</svg>');
            jQuery('.ui-datepicker-header > .ui-datepicker-next').append('<svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-gray-500 hover:text-violet-700 cursor-pointer" fill="none" viewBox="0 0 24 24" stroke="currentColor"> <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5l7 7-7 7" /> </svg>' +
                '</svg>');
        },

    });

    const ch32Dialog = '#ch32Dialog';

    jQuery(ch32Dialog).dialog({
        autoOpen: false,
        modal: true,
        width: '554px',
        resizable: false,
        draggable: false,
        closeOnEscape: true,
        dialogClass: 'no-close',
        closeText: '',
        buttons: [
            {
                text: 'Close',
                class: 'mr-4 h-11 inline-flex items-center shadow-xs justify-center px-5 py-3 border border-gray-300 text-base font-medium rounded-lg text-gray-700 hover:bg-gray-50 bg-white focus:outline-none focus:ring-4 focus:ring-gray-100',
                click: function () {
                    jQuery(this).dialog('close');
                }
            },
            {
                text: 'Confirm',
                class: 'h-11 inline-flex items-center shadow-xs justify-center px-5 py-3 text-base font-medium rounded-lg border border-violet-700 text-white hover:bg-violet-600 bg-violet-700 focus:outline-none focus:ring-4 focus:ring-violet-100',
            }
        ],
        create() {
            jQuery(this).closest('.ui-dialog').find('.ui-dialog-buttonset').addClass('md:text-right');
            jQuery(this).closest('.ui-dialog').find('.ui-button-icon-space').remove('.ui-button-icon-space');
            jQuery(this).closest('.ui-dialog').find('.ui-icon-closethick').remove('.ui-icon-closethick');
            jQuery(this).closest('.ui-dialog').find('.ui-dialog-titlebar-close').append('<svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 text-gray-300 hover:text-violet-700" fill="none" viewBox="0 0 24 24" stroke="currentColor">\n' +
                '  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />\n' +
                '</svg>');
        }
    });

    jQuery('.openDialog').on('click', function () {
        jQuery(ch32Dialog).dialog('open');
    });

    jQuery.ajax({
        url: 'https://api2.binance.com/api/v3/ticker/24hr',
        method: 'GET',
        dataType: 'json',
        success: function (data) {
            console.log(data);
            // loop through the data and create a new div for each product
            for (let i = 0; i < data.length; i++) {
                let hours24 = data[i];
                let hours24Div = document.createElement('tr');
                hours24Div.classList.add('product');
                hours24Div.setAttribute('data-id', hours24.id);
                hours24Div.innerHTML = `
                    <tr class="product-info">
                        <td class="px-6 py-4 whitespace-nowrap">
                        <div class="text-sm font-medium text-gray-900">${hours24.symbol}</div>
                        </td>
                        <td class="px-6 py-4 whitespace-nowrap">${hours24.priceChange}</td>
                        <td class="px-6 py-4 whitespace-nowrap">${hours24.priceChangePercent}</td>
                        <td class="px-6 py-4 whitespace-nowrap">${hours24.weightedAvgPrice}</td>
                        <td class="px-6 py-4 whitespace-nowrap">${hours24.prevClosePrice}</td>
                        <td class="px-6 py-4 whitespace-nowrap">${hours24.count}</td>      
                    </tr>
                `;
                document.querySelector('#products').appendChild(hours24Div);
            }
        },
        error: function (err) {
            console.log(err);
        },
    });

    jQuery(document).ajaxStart(function () {
        jQuery('#loading').show();
    });

    jQuery(document).ajaxComplete(function () {
        jQuery.snackbar({
            heading: 'Notification',
            text: 'Ajax data has been loaded',
            position: 'top-right',
            loader: false,
            stack: true,
            allowSnackbarClose: false,
            hideAfter: 5000,
        });
        jQuery('#loading').hide();
    });

    $(function () {
        var availableTags = [
            "ActionScript",
            "AppleScript",
            "Asp",
            "BASIC",
            "C",
            "C++",
            "Clojure",
            "COBOL",
            "ColdFusion",
            "Erlang",
            "Fortran",
            "Groovy",
            "Haskell",
            "Java",
            "JavaScript",
            "Lisp",
            "Perl",
            "PHP",
            "Python",
            "Ruby",
            "Scala",
            "Scheme"
        ];
        $("#tags").autocomplete({
            source: availableTags
        });
    });

});
